// Slider ver 2.0
if (window.matchMedia("(max-width: 767px)").matches) {
  // Viewport is more or equal to 768 pixels wide
 const swiper = new Swiper('.swiper', {
  // Optional parameters
  direction: 'horizontal',
  loop: true,

  // If we need pagination
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },

  // Navigation arrows
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },

  // And if we need scrollbar
  scrollbar: {
    el: '.swiper-scrollbar',
  },

  //Slide per View

  
 })

} else if (window.matchMedia("(max-width: 1199px)").matches) {
  // Viewport is more or equal to 768 pixels wide
 const swiper = new Swiper('.swiper', {
  // Optional parameters
  direction: 'horizontal',
  loop: true,

  // If we need pagination
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },

  // Navigation arrows
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },

  // And if we need scrollbar
  scrollbar: {
    el: '.swiper-scrollbar',
  },

  //Slide per View 768px - 2.5 1200px - 4
  slidesPerView: 2.5,
  spaceBetween: 18,
  
 })

} else if (window.matchMedia("(max-width: 9999px)").matches) {
  // Viewport is more or equal to 768 pixels wide
 const swiper = new Swiper('.swiper', {
  // Optional parameters
  direction: 'horizontal',
  loop: false,

  // If we need pagination
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },

  // Navigation arrows
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },

  // And if we need scrollbar
  scrollbar: {
    el: '.swiper-scrollbar',
  },

  //Slide per View 768px - 2.5 1200px - 4
  slidesPerView: 4,
  spaceBetween: 18,
    
 })
  
}

